import React from 'react'
import Group3 from './index'


export default function Group3List({ informs }) {
    const InformElements = informs.map(information =>
     <Group3 key={information.id} information={information} />)
    return (
        <div className="container-group3">
            <h1 className="gr3h1">What Pros Are Saying...</h1>
            <div className="cards">
                    {InformElements}
            </div>
        </div>
    )

}